<template>
  <div>
    <mrc_ess_iconGroup :is_disabled='$store.state.iconstatus'
                       :showReturn='false'
                       :showEdit="hasBtn('edit-staff-management')"
                       :show_del="hasBtn('delete-staff-management')"
                       @handleEdit='handleEdit'
                       @handleDel='handleDel'></mrc_ess_iconGroup>

    <v-tabs v-model="tab"
            grow
            background-color="#eeeef0"
            @change="tabchange"
            color='primary'
            dark>
      <v-tab v-for="item in items"
             style="color:black"
             :key="item.tab">
        {{ item.tab}}
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item>
        <staffmaterial :iconstatus='readonly'></staffmaterial>
      </v-tab-item>

      <v-tab-item>
        tree 树
      </v-tab-item>

    </v-tabs-items>
  </div>
</template>

<script>
import { getStaffPropertyGroupDetail } from '@/service/api/staffmanagement.js'
import { deleteStaff } from '@/service/api/staffmsg.js'
import staffmaterial from '../modules/staffmaterial.vue'
// import treelist from '../modules/treelist'
export default {
  components: {
    staffmaterial,
  },
  props: {
  },
  data () {
    return {
      tab: 0,
      readonly: true,//默认只读
      list: {}
    };
  },
  beforeDestroy () {
    this.$store.commit('setTab', '')
  },
  created () {
  },
  async mounted () {
    // console.log('渲染了');
    this.readonly = this.$store.state.iconstatus
    this.$route.meta.title = this.$store.getters.subPage_name
    this.tab = this.$store.state.tab
    this.$store.commit('setTab', this.tab)
  },
  methods: {
    tabchange () {
      this.$store.commit('setTab', this.tab)
    },
    handleEdit () {
      this.readonly = !this.readonly
      this.$store.commit('set_iconstatus', this.readonly)
    },
    handleDel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
        deleteStaff({ uuid: this.$store.state.uuid })
          .then(res => {
            if (res.code == 200) {
              this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
              this.$router.push({
                name: 'staffmanagement'
              })
            }
            else {
              this.$store.commit('newsTips', res.msg)
            }
          })
      })
    },

  },
  computed: {
    items () {
      return [
        { tab: this.$t('label_Employee_profile') },
        { tab: 'Propety' },
      ]
    }
  },
  watch: {
    tab (v) {
      console.log(v);
    }
  },
};
</script>

